.charaHeader {
    display: inline;
}

.brighteName, .lalehName, .lydiName,
.nakoaName, .rinName, .robynName, 
.suminaName {
    font-size: 210%;
    font-family: 'Playfair Display', serif;
    display: flex;
    justify-content: center;
    background-color: #d6d6d6;
}

.brighteName {
    color: #514a94;
}

.lalehName {
    color: #a57a38;
}

.lydiName {
    color: #92c6e0;
}

.nakoaName {
    color: #b55038;
}

.rinName {
    color: #3f6a3a;
}

.robynName {
    color: #5b7086;
}

.suminaName {
    color: #b81828;
}

.titleInfo {
    display: inline-block;
    flex-direction: column;
    text-align: left;
    margin-left: 10px;
    line-height: 0.2;  
}

.pronounciation {
    display: flex;
    justify-content: center;

    color: #939494;
    font-size: 14px;
    font-style: italic;
}

.cardImg, .fullbodyImg {
    width: 175px;
    height: 255px;

    margin: 0px 5px -10px 5px;
}

.cardImg {
    border: 1px solid grey;
    border-radius: 5px;
}

.summary {
    display: inline-block; 
    text-align: left; 
    margin-left: 5px;
    margin-top: 30px;
}

.summAtt {
    margin-top: -10px;
}

.more {
    display: inline-flex;
    justify-content: center;
}

.collapseDetails {
    text-align: right;
    width: 50%;
    margin-right: 10px;
}

.brighteDetailTitle, .lalehDetailTitle,
.lydiDetailTitle, .nakoaDetailTitle,
.rinDetailTitle, .robynDetailTitle, 
.suminaDetailTitle {
    font-weight: bold;
}

.brighteDetailTitle {
    color: #514a94;
}

.lalehDetailTitle {
    color: #a57a38;
}

.lydiDetailTitle {
    color: #92c6e0;
}

.nakoaDetailTitle {
    color: #b55038;
}

.rinDetailTitle {
    color: #3f6a3a;
}

.robynDetailTitle {
    color: #5b7086;
}

.suminaDetailTitle {
    color: #b81828;
}

.brighteBlurb, .lalehBlurb, .lydiBlurb,
.nakoaBlurb, .rinBlurb, .robynBlurb,
.suminaBlurb {
    font-size: 16px;
    max-height: 55px;
    margin-top: 0;
    overflow-x: hidden;
    overflow-y: auto;
    color: #939494;
}

.brighteBlurb::-webkit-scrollbar,
.lalehBlurb::-webkit-scrollbar,
.lydiBlurb::-webkit-scrollbar,
.nakoaBlurb::-webkit-scrollbar,
.rinBlurb::-webkit-scrollbar,
.robynBlurb::-webkit-scrollbar,
.suminaBlurb::-webkit-scrollbar {
    -webkit-appearance: none;
    width: 8px;
    left: 10px;
}

.brighteBlurb::-webkit-scrollbar-track,
.lalehBlurb::-webkit-scrollbar-track,
.lydiBlurb::-webkit-scrollbar-track,
.nakoaBlurb::-webkit-scrollbar-track,
.rinBlurb::-webkit-scrollbar-track,
.robynBlurb::-webkit-scrollbar-track,
.suminaBlurb::-webkit-scrollbar-track {
    border-left: 4px white solid;
    background-clip: padding-box;
}

.brighteBlurb::-webkit-scrollbar-track {
    background: #514a94;
}

.lalehBlurb::-webkit-scrollbar-track {
    background: #a57a38;
}

.lydiBlurb::-webkit-scrollbar-track {
    background: #92c6e0;
}

.nakoaBlurb::-webkit-scrollbar-track {
    background: #b55038;
}

.rinBlurb::-webkit-scrollbar-track {
    background: #3f6a3a;
}

.robynBlurb::-webkit-scrollbar-track {
    background: #5b7086;
}

.suminaBlurb::-webkit-scrollbar-track {
    background: #b81828;
}


.brighteBlurb::-webkit-scrollbar-thumb,
.lalehBlurb::-webkit-scrollbar-thumb,
.lydiBlurb::-webkit-scrollbar-thumb,
.nakoaBlurb::-webkit-scrollbar-thumb,
.rinBlurb::-webkit-scrollbar-thumb,
.robynBlurb::-webkit-scrollbar-thumb,
.suminaBlurb::-webkit-scrollbar-thumb {
    background-color: rgba(230, 230, 230, 0.5);
    -webkit-box-shadow: 0 0 4px rgba(255,255,255,.5);
    border-left: 4px white solid;
    background-clip: padding-box;
}

.brighteAttribute, .lalehAttribute,
.lydiAttribute, .nakoaAttribute,
.rinAttribute, .robynAttribute,
.suminaAttribute {
    font-weight: bold;
    display: inline-block;
    padding: 0px 3px 0px 3px;
    margin-bottom: -10px;
} 

.brighteAttribute {
    background-color: rgba(81, 74, 148, 0.4);
}

.lalehAttribute {
    background-color: rgb(165, 122, 56, 0.4);
}

.lydiAttribute {
    background-color: rgb(146, 198, 224, 0.4);
}

.nakoaAttribute {
    background-color: rgb(181, 80, 56, 0.4);
}

.rinAttribute {
    background-color: rgb(63, 106, 58, 0.4);
}

.robynAttribute {
    background-color: rgb(91, 112, 134, 0.4);
}

.suminaAttribute {
    background-color: rgba(184, 24, 40, 0.4);
}

.value {
    display: inline-block;
    margin-left: 10px;
    margin-bottom: -10px;
    color: #939494;
}

