.changeNavBGC {
    width: 100%;
    height: 100%;
    background-color: #1d1d1d;
}

.joker_body {
    width: 100%;
    height: 110%;
    display: flex;
  
    font-size: 120%;
    font-family: Roboto, sans-serif;
    background-color: #1d1d1d;
}

.jokerRedIcon {
    width: 50px;
    height: 50px;
    margin: 20px;
    padding-left: 20px;
    top: 0;
    position: absolute;
}

.joker_details {
    display: flex;
    flex-direction: column;
}

.jokerTitle {
    color: white;
    font-size: 350%;
    font-family: 'Playfair Display', serif;
}

.jokerExcerpt {
    width: 55%;
    padding-left: 22.5%;
    padding-right: 22.5%;
    text-align: center;
    color: grey;
}

.joker_selection {
    width: 100%;
    display: inline-block;
    justify-content: center;
    margin-bottom: 50px;
    background:
    linear-gradient(to top left,
        rgba(0,0,0,0) 0%,
        rgba(0,0,0,0) calc(50% - 0.8px),
        rgba(128,128,128,1) 50%,
        rgba(0,0,0,0) calc(50% + 0.8px),
        rgba(0,0,0,0) 100%),
    linear-gradient(to top right,
        rgba(0,0,0,0) 0%,
        rgba(0,0,0,0) calc(50% - 0.8px),
        rgba(128,128,128,1) 50%,
        rgba(0,0,0,0) calc(50% + 0.8px),
        rgba(0,0,0,0) 100%);
}

.X {
    width: 100%;
}

.divider {
    height: 300px;
}

.topInstruct {
    color: #939494;
    font-size: 80%;
    margin-top: -300px;
}

.joker_selectionR2 {
    width: 45%;
    margin-left: 27.5%;
    display: flex;
    justify-content: space-evenly;
}

.jokerSectorIcons {
    width: 70px;
    height: 70px;
}

.jokerSectorIcons:hover {
    cursor: pointer;
}

.jokerSector {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.jokerSectorTitle {
    color: white;
    font-size: 300%;
    font-family: 'Playfair Display', serif;
}

.jokerSectorIconSpecific {
    width: 85px;
    height: 85px;
}

.orgName {
    color: white;
    font-weight: bold;
}

.orgDescription {
    width: 55%;
    margin-left: 22.5%;
    color: grey;
    font-size: 17px;
}