.koranin, .sector {
    width: 100%;
    height: 100%;
    display: flex;
  
    font-size: 120%;
    font-family: Roboto, sans-serif;
}

.details {
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
}

.main_image {
    min-height: 100%;
    min-width: 40%;
    margin-top: -85px;
}

.image {
    min-height: 100%;
    min-width: 40%;
}

.title_intro {
    color: #939494;
    font-size: 90%;
}

.title {
    font-size: 350%;
    font-family: 'Playfair Display', serif;
    line-height: 65%;
}

.icon {
    width: 40px;
    height: 40px;
    padding: 10px;
    margin-top: 3.5%;
}

.excerpt {
    padding-left: 10%;
    padding-right: 10%;
    text-align: left;
    color: grey;
}

.body b {
    font-size: 140%;
}

.down_arrow, .up_arrow {
    width: 5%;
    height: 5%;

    margin-left: 47.5%;
    cursor: pointer;
}

.down_arrow {
    margin-top: 5%;
}

.up_arrow {
    margin-bottom: 5%;
}

.arrow_instruct {
    color: #939494;
    font-size: 80%;
}

button, button:focus{
    outline: none;
    background: transparent;
    border: 1px solid transparent;
}
  
button:active {
    outline: none;
    background: transparent;
    border: 1px solid transparent;
}

.systemTitle {
    font-weight: bold;
    color: black;
}

.systemDetail {
    border-left: 2px solid black;
    padding-left: 10px;
}